.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.login-page {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: #2d3a4b;
}
.login-page .title {
  font-size: 26px;
  font-weight: 400;
  color: #eee;
  margin: 0px auto 40px auto;
  text-align: center;
  font-weight: bold;
}
.login-page .login-form {
  position: absolute;
  left: 0;
  right: 0;
  width: 520px;
  max-width: 100%;
  padding: 35px 35px 15px 35px;
  margin: 120px auto;
  padding: 10px 0;
}
.login-page .form-body {
  border-radius: 5px;
  width: 100%;
  background: rgba(0, 0, 0, 0.1);
  padding: 30px 0 0 0;
  text-align: center;
}
.login-page .form-item {
  display: inline-block;
  color: #454545;
  width: 70%;
}
.login-page .form-item .ant-form-explain {
  float: left;
}
.login-page .form-button {
  width: 60%;
}
